/**
 * 
 */
function getStatusIcon(status) {
  switch (status) {
    case 'completed':
      return '';
    case 'in-progress':
      return '';
    case 'skipped':
      return '';
    case 'pending':
    default:
      return '';
  }
}

/**
 * 
 */
function getStatusText(status) {
  switch (status) {
    case 'completed':
      return 'Finished';
    case 'in-progress':
      return 'In progress';
    case 'skipped':
      return 'Skipped';
    case 'pending':
    default:
      return 'Not started';
  }
}

