/**
 * 
 */
async function navigateToPreviousImage() {
  //  FIX: Additional navigation lock check
  if (appState.navigation.isNavigating) {
    console.log('[Navigation] navigateToPreviousImage blocked - already navigating');
    return;
  }

  if (!appState.currentPlant || !appState.currentImage) {
    console.log('');
    return;
  }
  
  try {
    // 
    const images = await plantDataManager.getPlantImages(
      appState.currentPlant.id, 
      appState.currentPlant.selectedViewAngle
    );
    
    if (images.length <= 1) {
      console.log('');
      return;
    }
    
    // 
    const currentIndex = images.findIndex(img => img.id === appState.currentImage.id);
    
    if (currentIndex === -1) {
      console.warn('');
      return;
    }
    
    // 
    const previousIndex = currentIndex === 0 ? images.length - 1 : currentIndex - 1;
    const previousImage = images[previousIndex];
    
    console.log(`${currentIndex + 1}${previousIndex + 1}`);
    
    // 
    await handleImageSelect(previousImage, true);
    
  } catch (error) {
    console.error(':', error);
    showError('', error.message);
  }
}

/**
 * 
 * @param {boolean} autoMode - 
 * @returns {boolean} 
 */
async function navigateToNextImage(autoMode = false) {
  //  FIX: Additional navigation lock check
  if (appState.navigation.isNavigating) {
    console.log('[Navigation] navigateToNextImage blocked - already navigating');
    return false;
  }

  if (!appState.currentPlant || !appState.currentImage) {
    console.log('');
    return false;
  }

  try {
    // 
    const images = await plantDataManager.getPlantImages(
      appState.currentPlant.id,
      appState.currentPlant.selectedViewAngle
    );

    if (images.length <= 1) {
      console.log('');
      return false;
    }

    // 
    const currentIndex = images.findIndex(img => img.id === appState.currentImage.id);

    if (currentIndex === -1) {
      console.warn('');
      return false;
    }

    // 
    if (currentIndex === images.length - 1) {
      if (autoMode) {
        console.log('');
        return false;
      }
      // 
    }

    // 
    const nextIndex = currentIndex === images.length - 1 ? 0 : currentIndex + 1;
    const nextImage = images[nextIndex];

    console.log(`${currentIndex + 1}${nextIndex + 1}`);

    // 
    await handleImageSelect(nextImage, true);
    return true;

  } catch (error) {
    console.error(':', error);
    showError('', error.message);
    return false;
  }
}

/**
 * 
 */
function handleAutoDirectionSelection() {
  if (!annotationTool) {
    showError('', '');
    return;
  }

  // 
  if (!annotationTool.keypoints || annotationTool.keypoints.length === 0) {
    showError('', '');
    return;
  }

  // 
  const currentType = window.PlantAnnotationTool?.annotationTool?.customAnnotationManager?.getCurrentCustomType?.();
  if (currentType && currentType.metadata && currentType.metadata.isDirectional === false) {
    showError('Auto Direction Disabled', '');
    return;
  }

  //  FIX: Read mode from UI selector before starting auto direction mode
  const modeSelector = document.getElementById('auto-direction-mode-selector');
  if (modeSelector && modeSelector.value) {
    console.log(`[] UI: ${modeSelector.value}`);
    annotationTool.autoDirectionMode = modeSelector.value;
  } else {
    // Fallback to longitudinal if no selection
    console.log('[] UIlongitudinal');
    annotationTool.autoDirectionMode = 'longitudinal';
    if (modeSelector) {
      modeSelector.value = 'longitudinal';
    }
  }

  // 
  const success = annotationTool.startAutoDirectionMode();

  if (!success) {
    // startAutoDirectionMode 
    return;
  }

  // 
  const autoDirectionBtn = document.getElementById('auto-direction-btn');
  if (autoDirectionBtn) {
    console.log('[] ');

    // 
    autoDirectionBtn.removeEventListener('click', handleAutoDirectionSelection);

    // 
    autoDirectionBtn.textContent = 'Exit Auto Mode';
    autoDirectionBtn.classList.add('active');

    // 
    const pauseHandler = () => {
      console.log('[] ');
      annotationTool.pauseAutoDirectionMode();
    };

    // 
    autoDirectionBtn.addEventListener('click', pauseHandler);

    // 
    autoDirectionBtn._pauseHandler = pauseHandler;
  }

  updateProgressInfo('');
}

/**
 * 
 */
function resetAutoDirectionButton() {
  const autoDirectionBtn = document.getElementById('auto-direction-btn');
  if (autoDirectionBtn) {
    console.log('[] ');

    // 
    if (autoDirectionBtn._pauseHandler) {
      autoDirectionBtn.removeEventListener('click', autoDirectionBtn._pauseHandler);
      autoDirectionBtn._pauseHandler = null;
    }

    // 
    autoDirectionBtn.textContent = 'Auto Direction';
    autoDirectionBtn.classList.remove('active');

    // 
    autoDirectionBtn.addEventListener('click', handleAutoDirectionSelection);
    
    console.log('[] ');
  }
}

